.css-typing p {
    border-right: .15em solid orange;
    font-family: 'Roboto Mono', monospace;
    font-size:xx-large;
    white-space: nowrap;
    overflow: hidden;
  }
  .css-typing p:nth-child(1) {
    width: 1.8em;
    opacity: 0;
    animation: type 1s steps(4);
    animation-delay: 2s;
    animation-fill-mode: forwards;
  }
  
  .css-typing p:nth-child(2) {
    width: 6.5em;
    opacity: 0;
    animation: type2 2s steps(11, end);
    animation-delay: 3s;
    animation-fill-mode: forwards;
  }
  
  .css-typing p:nth-child(3) {
    width: 8.5em;
    opacity: 0;
    animation: type3 1s steps(13), blink .5s step-end infinite alternate;
    animation-delay: 5s;
    animation-fill-mode: forwards;
  }
  
  @keyframes type {
    0% {
      width: 0;
    }
    1% {
        opacity: 1;
      }
    99.9% {
      border-right: .15em solid orange;
    }
    100% {
        opacity: 1;
      border: none;
    }
  }
  
  @keyframes type2 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    99.9% {
      border-right: .15em solid orange;
    }
    100% {
      opacity: 1;
      border: none;
    }
  }
  
  @keyframes type3 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes blink {
    50% {
      border-color: transparent;
    }
  }

  .fa-star{
    font-size: 3.5vw;
    margin-right: 2vw;
    color: #4B5D67;
  }

  .checked{
    color:	#ffd700;
  }
